.wrapper {
    padding: 50px 80px 50px;
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 40vw;
    height: 80vh;
    background-color: #fff;
    box-shadow: 0 10px 40px -10px rgba(#818181, .5);
}

.closeModal {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 20px;
    color: white;
    background-color: #1e58ff;
    border: none;
}


.closeModal:hover {
    color: #1e58ff;
    background-color: white;
}