.button {
    font-size: .8em;
    text-decoration: none;
    padding: 7px 12px;
    font-weight: 700;
    background: none;
    border: 2px solid #1e58ff;
    color: white;
    background-color: #1e58ff;
    cursor: pointer;
}

.secondary {
    @extend .button;

    background-color: white;
    color: #1e58ff;
    font-weight: 500;
    font-size: 12px;
    margin-top: 35px;
    width: 50%;
}