@keyframes appear {
    0% {
        opacity: 0;
        top: 35px;
    }

    100% {
        opacity: 1;
        top: 0;
    }
}




.wrapper {
    position: relative;
    list-style: none;
    padding: 0px 30px 50px 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    animation: appear .5s ease;
}

.image {
    flex-shrink: 0;
    margin-right: 30px;
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.imageNone {
    @extend .image;

    background: url('https://unsplash.it/200/200');
    background-size: cover;
}

.about {
    color: #1E58FF;
    margin: 10px 0 20px;
    font-weight: 500;
}

span {
    color: black;
    font-weight: normal;
}

.description {
    color: #1E58FF;
    font-weight: normal;
}