.radio {
    position: relative;
    display: inline-flex;
    align-items: center;
    margin: 20px 20px 0 0;
    font-weight: 700;
    color: #1e58ff;
}

.input {
    opacity: 0;
    position: absolute;
}

.radioButton {
    margin-right: 5px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 2px solid #1e58ff;
}

input:checked+.radioButton {
    background-color: #1e58ff;
    box-shadow: inset 0 0 0 2px white;
}